"use client"
import { Search, ShoppingBag, User } from "lucide-react";
import { useState } from "react";

export default function Header() {
    const [isOpen, setIsOpen] = useState(false);
    const [selected, setSelected] = useState("Shop");

    const handleSelect = (option) => {
        setSelected(option);
        setIsOpen(false);
    };

    return (
        <>
            <div className="flex bg-black items-center justify-center py-1">
                <p className="text-white">Sign up and get 20% off to your first order. <a href="#">Sign Up Now</a></p>
            </div>
            <nav className="flex justify-between px-20 py-5 items-center">
                <div className="flex gap-8 mx-20 items-center">
                    <a href="#" className="text-4xl font-extrabold">D-SHOP</a>
                    <div className="flex gap-6 mx-10 text-xl items-center p-3 relative">
                        <a href="#">Shop</a>
                        <a href="#">On Sale</a>
                        <a href="#">New Arrivals</a>
                        <a href="#">Brands</a>
                    </div>
                </div>
                <div className="flex-grow max-w-2xl hidden md:block mr-20">
                    <div className="relative">
                        <input
                            type="text"
                            placeholder="Search for products..."
                            className="w-full px-4 py-2 pl-10 rounded-full bg-gray-100"
                        />
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={20} />
                    </div>
                </div>
                <div className="flex gap-4 mr-10 items-center justify-center">
                    <ShoppingBag className="text-gray-600 cursor-pointer hover:text-gray-900 transition-colors" size={30} />
                    <User className="text-gray-600 cursor-pointer hover:text-gray-900 transition-colors" size={30} />
                </div>
            </nav>
        </>
    )
}